@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Libre+Franklin:ital,wght@0,400;0,500;1,400&family=Raleway:wght@500;600;700;800;900&display=swap");
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  line-height: 1;
}

body {
  word-wrap: break-word;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  font-family: "Raleway", sans-serif;
  font-weight: 900;
  line-height: 1.4;
}

h1 {
  font-size: 140%;
}

h2 {
  font-size: 130%;
}

h3 {
  font-size: 105%;
}

h4, h5, h6 {
  font-size: 100%;
}

ul,
ol {
  margin: 2.6rem 0 2.6rem 2rem;
}

li {
  margin-bottom: 0.8rem;
  line-height: 1.5;
}

dl {
  display: flex;
  flex-wrap: wrap;
  margin: 0 1.2rem 1.8rem;
  line-height: 1.5;
}

dt {
  flex: 0 0 30%;
}
dt::after {
  content: ":";
}

dd {
  flex: 0 0 70%;
  word-break: break-word;
}

nav ul {
  margin: 0;
  list-style: none;
}

p {
  margin: 0 0 1rem;
  line-height: 1.5;
}
@media only screen and (min-width: 768px) {
  p {
    margin-bottom: 2rem;
  }
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

img {
  display: block;
  -ms-interpolation-mode: bicubic;
}

q::before,
q::after {
  content: "";
}

input,
select,
textarea {
  appearance: none;
  border: none;
  border-radius: 0;
  background: none;
}

input:not([type=checkbox]),
input:not([type=radio]) {
  line-height: 1;
}

@font-face {
  font-family: "social";
  src: url("/images/icons/social.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
header {
  display: flex;
  flex-direction: column;
  flex: 0 0 auto;
  padding: 0.8rem 0;
  font-family: Raleway, sans-serif;
  font-size: 1.8rem;
  font-weight: 500;
  background: white;
}
header h1 {
  padding: 1.4rem;
  font-size: 4.2rem;
  font-weight: 900;
  line-height: 1;
  text-align: center;
}
header h1 a {
  display: flex;
  justify-content: center;
  color: hsl(207,54%,32%);
  text-decoration: none;
}
header h1 span {
  display: block;
}
header h1 span.j {
  padding-bottom: 0.8rem;
}
header h1 span.s {
  padding-top: 0.8rem;
}
header nav ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 auto;
  max-width: 42rem;
}
header nav li {
  margin: 0.8rem 0.4rem;
}
header nav li a {
  display: block;
  position: relative;
  padding: 0.6rem 1.2rem 1.2rem;
  text-decoration: none;
  text-transform: uppercase;
  color: hsl(207,54%,32%);
}

header nav li a:hover:after,
body[data-controller=index] header nav li.about a:after,
body[data-controller=case-study] header nav li.case-studies a:after,
body[data-controller=case-studies] header nav li.case-studies a:after,
body[data-controller=portfolio] header nav li.portfolio a:after,
body[data-controller=work-history] header nav li.work-history a:after,
body[data-controller=contact] header nav li.contact a:after {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  left: 0.5rem;
  width: calc(100% - 1rem);
  height: 0.4rem;
  background: hsl(207,54%,32%);
}

header nav li a:hover:after {
  background: hsl(175,75%,40%);
}

@media only screen and (min-width: 768px) {
  header {
    flex-direction: row;
    justify-content: space-between;
    padding: 0;
    font-size: 1.6rem;
    background: hsla(255,100%,100%,1);
  }
  header h1 {
    padding: 3rem 4rem;
  }
  header nav ul {
    max-width: none;
    margin: 1.8rem 2.4rem 0 0;
  }
  header nav ul li {
    margin: 0;
  }
  header nav ul li a {
    padding: 2rem 1.5rem 1.9rem;
  }
  html.js header {
    position: fixed;
    z-index: 500;
    backdrop-filter: blur(1rem);
    transition: margin 0.5s, background 0.25s;
  }
  html.js body.fixed-header header {
    background: hsla(207,54%,95%,0.75);
  }
  html.js body.hidden-header header {
    margin-top: -11rem;
  }
}
body > footer {
  padding: 2rem;
  color: white;
  text-align: center;
  background: hsl(182,18%,51%);
}
@media (hover: none) {
  body > footer {
    padding-bottom: 4rem;
  }
}
body > footer nav {
  margin: 2rem auto 4rem;
  font-family: Raleway, sans-serif;
}
body > footer nav li {
  margin-bottom: 1rem;
}
body > footer nav a {
  display: inline-block;
  padding: 1rem;
  text-decoration: none;
  text-transform: uppercase;
}
body > footer p.copyright {
  margin: 0;
  font-size: 1.4rem;
}
body > footer a {
  color: white;
}
@media only screen and (min-width: 768px) {
  body > footer {
    font-size: 1.6rem;
  }
  body > footer nav {
    width: 66rem;
    margin: 1rem auto 3rem;
  }
  body > footer ul {
    display: flex;
    justify-content: space-between;
    margin: auto;
  }
  body > footer ul li {
    margin: 0;
  }
}

section.screens img[data-src]:not(.loaded) {
  height: 0;
}
section.screens figure[role=group] {
  margin: 6rem auto;
}
section.screens figure[role=group] section {
  display: flex;
  overflow-x: scroll;
  -webkit-overflow-scrolling: touch;
  padding: 0 2rem 2rem 2rem;
}
section.screens figure[role=group] section::after {
  content: "";
  flex: 0 0 2rem;
  display: block;
  height: 2rem;
}
section.screens figure[role=group] figure {
  flex: 0 0 30rem;
  margin-right: 2rem;
  border-radius: 0.4rem;
  border: 0.1rem solid hsl(175,25%,90%);
  overflow: hidden;
  position: relative;
}
section.screens figure[role=group] figure:last-child {
  margin-right: 0;
}
section.screens figure[role=group] figure img {
  width: 100%;
}
section.screens figure[role=group] a.new-tab {
  position: initial;
  height: 100%;
}
section.screens figure[role=group] figcaption {
  margin-top: 0;
  max-width: none;
}
section.screens.group figure[role=group] {
  margin: 0 auto;
}
section.screens.group figure[role=group] figure {
  height: 22.5rem;
}
@media only screen and (min-width: 768px) {
  section.screens.group {
    margin: 0;
  }
  section.screens figure[role=group] {
    padding: 0;
  }
  section.screens figure[role=group] section {
    padding-right: 4rem;
    padding-left: 4rem;
  }
  section.screens figure[role=group] section::after {
    flex-basis: 4rem;
  }
}
@media only screen and (min-width: 840px) {
  section.screens figure[role=group] section {
    flex-wrap: wrap;
    justify-content: center;
    padding-bottom: 0;
    overflow: visible;
  }
  section.screens figure[role=group] section::after {
    display: none;
  }
  section.screens figure[role=group] figure {
    flex-basis: 23.5%;
    margin: 0 2% 2rem 0;
  }
  section.screens figure[role=group] figure:first-child, section.screens figure[role=group] figure:nth-child(5n) {
    margin-left: 0;
  }
  section.screens figure[role=group] figure:nth-child(4n) {
    margin-right: 0 !important;
  }
  section.screens.group figure[role=group] figure {
    height: 16vw;
  }
}
@media only screen and (min-width: 1280px) {
  section.screens.group figure[role=group] figure {
    height: 21.2rem;
  }
}

figure.callout {
  margin: 4rem auto;
}
figure.callout img {
  width: 100%;
  border-radius: 0.4rem;
  border: 0.1rem solid #e6e6e6;
}

figcaption {
  max-width: 60rem;
  margin: 1rem auto 0;
  font-size: 1.6rem;
  font-style: italic;
  line-height: 1.4;
  text-align: center;
}

figure.device img {
  border: none;
}

@media only screen and (min-width: 768px) {
  figure.callout img {
    width: auto;
    height: 36rem;
    margin: auto;
  }
}
@media only screen and (min-width: 1280px) {
  section.screens:not(.group) {
    max-width: 128rem;
    margin: auto;
  }
}
#screen-nav {
  margin: 1rem 0 4rem;
  color: #efefef;
}
#screen-nav ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
#screen-nav li {
  margin: 1rem;
}
#screen-nav a {
  display: block;
  position: relative;
  width: 3rem;
  height: 3rem;
  padding-left: 3rem;
  overflow: hidden;
}
#screen-nav a:before {
  content: "";
  display: block;
  position: absolute;
  top: 0.5rem;
  left: 0.5rem;
  width: 2rem;
  height: 2rem;
  border: 0.2rem solid #efefef;
  border-radius: 50%;
}
#screen-nav a:hover:before {
  background: #de2121;
}
#screen-nav li.selected a:before {
  background: #efefef;
}

html {
  height: 100%;
  font-size: 62.5%;
}

body {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  height: 100%;
  font-family: "Libre Franklin", sans-serif;
  font-size: 1.8rem;
  background: white;
  text-rendering: optimizeLegibility;
}
body > * {
  width: 100%;
}

main {
  flex: 1 1 auto;
  color: black;
}
main h1, main h2, main h3, main h4 {
  padding: 3rem 2rem 2rem;
  color: hsl(176,53%,37%);
  text-align: center;
}
main h1, main h2 {
  font-size: 3.2rem;
}
main h2, main h3 {
  font-weight: 700;
}
main h3 {
  font-size: 2.6rem;
}
main h4 {
  font-size: 2rem;
  font-weight: 600;
  color: hsl(0,0%,30%);
}
main a {
  color: hsl(207,54%,32%);
}
main a:hover {
  text-decoration: none;
}
main nav {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 0 2rem 4rem;
}

section.content {
  margin: 4rem 2rem;
}
section.content > *:first-child {
  margin-top: 0;
  padding-top: 0;
}
section.content h2, section.content h3 {
  padding-right: 0;
  padding-left: 0;
}
section.content p {
  margin-bottom: 2.6rem;
  line-height: 1.6;
}

a.primary-action,
li.primary-action input {
  display: inline-block;
  min-width: 26rem;
  padding: 1.6rem 4rem;
  font-family: Raleway, sans-serif;
  font-size: 1.8rem;
  font-weight: 600;
  color: hsl(0,0%,100%);
  text-align: center;
  text-decoration: none;
  background: hsl(2,86%,63%);
  border-radius: 0.4rem;
}
a.primary-action:hover,
li.primary-action input:hover {
  background: hsl(2,86%,53%);
}

p.error {
  display: flex;
  margin: 2rem;
  padding: 2rem;
  font-family: Raleway, sans-serif;
  font-weight: 600;
  color: hsl(0,0%,100%);
  background: hsl(175,55%,50%);
  border-radius: 0.6rem;
}
p.error:before {
  content: "!";
  display: block;
  flex: 0 0 3rem;
  height: 3rem;
  margin-right: 2rem;
  padding: 0.6rem 0.4rem 0.2rem;
  font-size: 2rem;
  font-weight: 900;
  line-height: 2rem;
  text-align: center;
  color: hsl(175,55%,50%);
  background: hsl(0,0%,100%);
  border-radius: 50%;
}

@media only screen and (max-width: 767px) {
  img[data-src]:not(.loaded) {
    height: 100%;
  }
}
@media only screen and (min-width: 768px) {
  img[data-src]:not(.loaded) {
    width: 100%;
  }
}

section.intro {
  padding: 4rem 2rem;
  background: hsl(207,54%,98%);
}
section.intro h1 {
  padding: 0;
}
section.intro p {
  margin: 2rem 0 0;
  font-family: Avenir Next, Libre Franklin, sans-serif;
  font-size: 2.4rem;
  font-weight: 500;
  color: hsl(176,33%,20%);
  text-align: center;
  line-height: 1.4;
}

@keyframes loading-indicator {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
#mask {
  position: fixed;
  width: 0;
  height: 0;
  overflow: hidden;
  top: 0;
  opacity: 0;
  background: hsla(0,0%,0%,0.95);
  background-color: hsla(0,0%,0%,0.9);
}
@supports (backdrop-filter: saturate(180%) blur(0.5rem)) {
  #mask {
    background-color: hsla(0,0%,0%,0.8);
    backdrop-filter: saturate(180%) blur(0.5rem);
  }
}
#mask.loading::before {
  content: "";
  display: block;
  width: 10rem;
  height: 10rem;
  position: absolute;
  top: calc(50% - 5rem);
  left: calc(50% - 5rem);
  border: 0.8rem solid #efefef;
  border-top-color: #666666;
  border-bottom-color: #666666;
  border-radius: 50%;
  animation: loading-indicator 1.25s ease 8;
}
#mask h1 {
  padding: 0.2rem 0;
  font-size: 1.8rem;
  color: #efefef;
}
@media only screen and (min-width: 768px) {
  #mask h1 {
    text-align: center;
  }
}
#mask img {
  width: 100%;
  height: calc(100% - 170px);
  margin: 2rem auto;
  z-index: 2;
  object-fit: contain;
}
#mask.enabled {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  padding: 2rem;
  z-index: 1000;
  opacity: 1;
}
#mask.screens {
  justify-content: space-between;
}

#mask-open-tab {
  width: 3rem;
  height: 3rem;
  padding-left: 3rem;
  position: absolute;
  top: 2rem;
  right: 6rem;
  background: url("/images/icons.svg") no-repeat;
  background-size: auto 2rem;
  background-position: -10.2rem 0.5rem;
  overflow: hidden;
}

#mask-close {
  width: 3rem;
  height: 3rem;
  padding-left: 3rem;
  position: absolute;
  top: 2rem;
  right: 1.5rem;
  background: url(/images/icons.svg) no-repeat;
  background-size: auto 2rem;
  background-position: -12.8rem 0.5rem;
  overflow: hidden;
}

a.new-tab {
  display: block;
  position: relative;
}
@media (hover: hover) {
  a.new-tab::after {
    content: "";
    display: block;
    width: 4rem;
    height: 4rem;
    position: absolute;
    top: calc(50% - 2rem);
    left: calc(50% - 2rem);
    background: url("/images/icons.svg") no-repeat;
    background-size: cover;
    background-position: -32rem 0;
    opacity: 0;
    transition: opacity 0.2s;
  }
  a.new-tab img {
    transition: filter 0.2s;
  }
  a.new-tab:hover::after {
    opacity: 1;
  }
  a.new-tab:hover img {
    filter: brightness(50%);
  }
}

@media only screen and (min-width: 768px) {
  main h1, main h2, main h3, main h4 {
    padding-right: 4rem;
    padding-left: 4rem;
    text-align: left;
  }
  main h3 {
    font-size: 2.4rem;
  }
  main nav {
    justify-content: flex-start;
    padding: 2rem 4rem;
  }
  html.js main {
    padding-top: 11rem;
  }
  section.intro {
    padding: 4rem;
  }
  section.intro p {
    text-align: left;
  }
  section.content {
    max-width: 88rem;
    margin: 6rem auto;
    padding: 0 4rem;
    font-size: 2rem;
  }
  p.error {
    margin: 4rem 0 6rem 4rem;
  }
}
@media only screen and (min-width: 1024px) {
  a.primary-action {
    padding: 1.4rem 2.4rem;
  }
  main > section,
main > article {
    max-width: 128rem;
    margin-right: auto;
    margin-left: auto;
    position: relative;
  }
  main h1 {
    font-size: 4.2rem;
  }
  main nav {
    position: absolute;
    right: 4rem;
    top: 3rem;
    padding: 0;
  }
}
@media only screen and (min-width: 1440px) {
  section.intro {
    padding: 6rem;
  }
  section.content {
    margin: 10rem auto;
  }
  html.js main {
    padding-top: 16rem;
  }
}
